.page-template-template-links .content .links-menu {
  width: 100%;
}
.page-template-template-links .content .links-menu__wrapper {
  display: flex;
  flex-flow: row wrap;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 25px;
}
.page-template-template-links .content .links-menu__item {
  min-width: 280px;
  flex: 1 1 30%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  gap: 20px;
}
.page-template-template-links .content .links-menu__item .menu-item * {
  margin: 0;
}
.page-template-template-links .content .links-menu__item .menu-item-title {
  font-family: var(--playfair);
  font-size: 24px;
  line-height: 26px;
  color: rgba(var(--primary-complement), 1);
  text-align: center;
  text-transform: uppercase;
  font-weight: bold;
}
.page-template-template-links .content .links-menu__item .menu-item-person {
  font-size: 18px;
  line-height: 22px;
  color: rgba(var(--text), 1);
  text-align: center;
}
.page-template-template-links .content .links-menu__item .menu-item-mail {
  font-size: 18px;
  line-height: 22px;
  color: rgba(var(--primary-color), 1);
  text-align: center;
  transition: 0.2s ease;
}
.page-template-template-links .content .links-menu__item .menu-item-mail:hover {
  color: rgba(var(--secondary-color), 1);
}